import {
  Keyboard,
  TouchableOpacity,
  TouchableWithoutFeedback,
  View,
} from 'react-native';
import {SafeAreaView, useSafeAreaInsets} from 'react-native-safe-area-context';
import {Color, NavigationName} from '../../../../../constants';
import {
  Divider,
  Icon,
  Input,
  Layout,
  List,
  ListItem,
} from '@ui-kitten/components';
import {useEffect, useRef, useState} from 'react';
import {useNavigation} from '@react-navigation/native';
import {
  IServicesDetail,
  IServicesHome,
} from '../../../../../model/interface/service.interface';
import {useAppSelector} from '../../../../../hooks/useRedux';
import {debounce, isEmpty} from 'lodash';
import TextCM from '../../../../components/Text';
import {useTranslation} from 'react-i18next';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
const SearchHomeScreen = () => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const inset = useSafeAreaInsets();
  const inputRef = useRef<any>(null);
  const [value, setValue] = useState('');
  const [listServices, setListServices] =
    useState<(IServicesHome | IServicesDetail)[]>();
  const ListServices = useTicketServices();
  const [debounceSearchText, setDebounceSearch] = useState<string>('');

  const debouncedSearch = debounce((text: string) => {
    setDebounceSearch(text);
  }, 300);

  const {accessToken} = useAppSelector(state => state.common);
  const {dataAccount} = useAppSelector(state => state.account);

  useEffect(() => {
    debouncedSearch(value);
    return () => {
      debouncedSearch.cancel();
    };
  }, [value]);

  useEffect(() => {
    if (!isEmpty(debounceSearchText)) {
      getListServices(debounceSearchText);
    } else {
      setListServices([]);
    }
  }, [debounceSearchText]);

  const getListServices = async (debouncedSearch: string) => {
    const params = {
      sort: ['displayOrder,asc', 'createdDate,desc'],
      page: 0,
      size: 10000,
    };
    const roleIds = dataAccount?.roles?.map((r: any) => r.id).join(',');
    const body = {
      textSearch: debouncedSearch,
      roleIds: roleIds,
    };
    const data = await ListServices.getListServives(body, params, accessToken);
    if (data) {
      setListServices(data.content);
    }
  };

  const handleServiceDetail = (item: IServicesHome | IServicesDetail) => {
    if (item.serviceId) {
      navigation.navigate(NavigationName.ServiceDetail, item);
    } else {
      navigation.navigate(NavigationName.CreateTicket, {dataTicket: item});
    }
  };

  const renderItem = ({
    item,
  }: {
    item: IServicesHome | IServicesDetail;
  }): React.ReactElement => {
    return (
      <ListItem
        title={props => (
          <TextCM {...props}>
            {'serviceName' in item ? item.serviceName : item.flowName}
          </TextCM>
        )}
        description={props => (
          <TextCM {...props}>
            {'description' in item && item.description ? item.description : ''}
          </TextCM>
        )}
        accessoryLeft={
          <Icon
            fill={'color' in item && item.color ? item.color : Color.BgPrimary}
            name={'color' in item ? 'grid-outline' : 'browser-outline'}
          />
        }
        accessoryRight={<Icon name="chevron-right-outline" />}
        onPress={() => handleServiceDetail(item)}
      />
    );
  };

  return (
    <TouchableWithoutFeedback onPress={() => Keyboard.dismiss()}>
      <SafeAreaView
        edges={['top']}
        style={{backgroundColor: Color.BgPrimary, flex: 1}}>
        <>
          <Input
            style={{
              backgroundColor: Color.BgPrimary,
              borderWidth: 0,
            }}
            allowFontScaling={false}
            textStyle={{color: '#fff'}}
            placeholderTextColor={'#fff'}
            ref={inputRef}
            placeholder={t('HM.search')}
            value={value}
            autoFocus
            status="basic"
            onChangeText={nextValue => setValue(nextValue)}
            accessoryLeft={
              <TouchableOpacity onPress={() => navigation.goBack()}>
                <Icon name="arrow-back" fill="#ffffff" />
              </TouchableOpacity>
            }
            accessoryRight={
              value ? (
                <TouchableOpacity onPress={() => setValue('')}>
                  <Icon name="close-outline" fill="#ffffff" />
                </TouchableOpacity>
              ) : undefined
            }
          />
          <Divider />
          <Layout
            style={{
              flex: 1,
              paddingBottom: inset.bottom,
              backgroundColor: '#fff',
            }}
            level="1">
            {value.length > 0 && (
              <List
                style={{backgroundColor: '#fff', flex: 1}}
                showsVerticalScrollIndicator={false}
                data={listServices}
                renderItem={renderItem}
                ListEmptyComponent={() => (
                  <View style={{margin: 10, flex: 1}}>
                    <TextCM>{t('CM.emptyData')}</TextCM>
                  </View>
                )}
              />
            )}
          </Layout>
        </>
      </SafeAreaView>
    </TouchableWithoutFeedback>
  );
};

export default SearchHomeScreen;
