import {TouchableOpacity, View} from 'react-native';
import {useState} from 'react';
import {IServicesDetail} from '../../../../../model/interface/service.interface';
import {IconArrowGrey} from '../../../../../assets/images';
import styles from './styles';
import TextCM from '../../../../components/Text';
import {useNavigation} from '@react-navigation/native';
import {NavigationName} from '../../../../../constants';
import {SizeDP} from '../../../../../constants/Size';

const RenderListItem = ({item}: {item: IServicesDetail[]}) => {
  const navigation = useNavigation<any>();
  const [viewSubItem, setViewSubItem] = useState<boolean>(false);

  const handleCreateContract = (item: IServicesDetail) => {
    navigation.navigate(NavigationName.CreateTicket, {dataTicket: item});
  };

  return (
    <View>
      {item.every(data => !data.groupModelerId) ? (
        <TouchableOpacity
          onPress={() => handleCreateContract(item[0])}
          style={styles.ctnItem}>
          <TextCM>{item[0].flowName}</TextCM>
        </TouchableOpacity>
      ) : (
        <>
          <TouchableOpacity
            onPress={() => setViewSubItem(!viewSubItem)}
            style={styles.ctnItem}>
            <TextCM>{item[0].groupModelerName}</TextCM>
            {!viewSubItem ? (
              <View>
                <IconArrowGrey width={SizeDP(16)} height={SizeDP(16)} />
              </View>
            ) : (
              <View style={{transform: [{rotate: '180deg'}]}}>
                <IconArrowGrey width={SizeDP(16)} height={SizeDP(16)} />
              </View>
            )}
          </TouchableOpacity>
          {viewSubItem &&
            item.map((subItem: IServicesDetail) => (
              <View style={[styles.ctnSubItem]} key={subItem.id}>
                <TouchableOpacity onPress={() => handleCreateContract(subItem)}>
                  <TextCM>{subItem.flowName}</TextCM>
                </TouchableOpacity>
              </View>
            ))}
        </>
      )}
    </View>
  );
};

export default RenderListItem;
