import {createNativeStackNavigator} from '@react-navigation/native-stack';
import NavigationName from '../constants/NavigationName';
import BottomTabNavigationERequest from './BottomTabERequest';
import Home from '../app/screens/home';
import ServiceDetail from '../app/screens/home/component/ServicesDetail';
import CreateTicketScreen from '../app/screens/home/component/CreateTicket';
import CreateTicketSuccessScreen from '../app/screens/home/component/CreateTicketSuccess';
import AddTicketRelative from '../app/screens/home/component/AddTicketRelative';
import ViewFlowDetail from '../app/screens/ticket-detail/components/ViewFlowDetail';
import ReviewFlow from '../app/screens/review-flow';
import Toast from 'react-native-toast-message';
import {PaperProvider} from 'react-native-paper';
import ViewFileScreenEcontract from '../app/screens/review-econtract-flow/components/ViewFile';
import {ApplicationProvider, IconRegistry} from '@ui-kitten/components';
import * as eva from '@eva-design/eva';
import {EvaIconsPack} from '@ui-kitten/eva-icons';
import {default as theme} from '../../custom-theme.json'; // <-- Import app theme
import {default as mapping} from '../../mapping.json'; // <-- Import app mapping
import TicketDetail from '../app/screens/ticket-detail';
import SearchTicketScreen from '../app/screens/search-ticket';
import SearchHomeScreen from '../app/screens/home/component/Search';
import {useAppDispatch, useAppSelector} from '../hooks/useRedux';
import {useEffect, useState} from 'react';
import ReviewEIDScreen from '../app/screens/review-eid';
import ReviewEcontractFlow from '../app/screens/review-econtract-flow';
import CreateContractScreen from '../app/screens/create-contract';
import ReviewEcollaboFlow from '../app/screens/review-ecollabo-flow';
import {useTranslation} from 'react-i18next';
import React from 'react';
import {useNavigation, useRoute} from '@react-navigation/native';
import {ActivityIndicator, View} from 'react-native';
import {Color} from '../constants';
import toastConfig from '../utils/ToastConfig';
import {useZutandStore} from '../zutand/store';
import {ServiceIdApp} from '../model/enum/common.enum';
import {useAccountSettingService} from '../hooks/apiHooks/useAccountInfoServices';
import {useTicketServices} from '../hooks/apiHooks/useTicketServices';
import {commonActions} from '../redux/slices';
import HomeListService from '../app/screens/home/component/ListService';
import NoAuthComponent from '../app/components/NoAuth';
const Stack = createNativeStackNavigator();
const AuthorizedNavigation = () => {
  const AccountSettingServices = useAccountSettingService();
  const {i18n} = useTranslation();
  const [isLoadInitData, setIsLoadInitData] = useState(true);
  const router = useRoute<any>();
  const navigation = useNavigation<any>();
  const type = router?.params?.type || '';
  const dispatch = useAppDispatch();
  const {language} = useAppSelector(state => state.common);
  const {setListMenuViewERequest} = useZutandStore();
  const ListServices = useTicketServices();
  const fetchListMenuViewErequest = async () => {
    // const data = await ListServices.login();
    // dispatch(commonActions.setAccessToken(data.access_token));
    const listMenuViewERequest = await AccountSettingServices.getMenuView(
      ServiceIdApp.EREQUEST,
    );
    setListMenuViewERequest(listMenuViewERequest);
  };

  useEffect(() => {
    fetchListMenuViewErequest();
    i18n.changeLanguage(language);
    setIsLoadInitData(false);
  }, []);

  useEffect(() => {
    if (type && !isLoadInitData) {
      handleParamsOpenAppERequest();
    }
  }, [type, isLoadInitData]);

  const handleParamsOpenAppERequest = () => {
    if (!router?.params) return;
    const paramsData = router?.params as any;
    navigation.navigate(NavigationName.BottomTabNavigationTicketDetail, {
      dataTicketId: paramsData.dataTicketId,
      status: paramsData.status,
      selectTabBottom: Number(paramsData.selectTabBottom),
      recipientDetailsId: paramsData.recipientDetailsId,
      userTaskId: paramsData.userTaskId,
      notifyActionType: paramsData.notifyActionType,
    });
  };

  if (isLoadInitData) {
    return (
      <View
        style={{
          flex: 1,
          justifyContent: 'center',
          alignItems: 'center',
        }}>
        <ActivityIndicator size={'large'} color={Color.BgPrimary} />
      </View>
    );
  }

  return (
    <>
      <IconRegistry icons={[EvaIconsPack]} />
      <ApplicationProvider
        customMapping={mapping as any}
        {...eva}
        theme={{...eva.light, ...theme}}>
        <PaperProvider>
          <Stack.Navigator
            // initialRouteName={NavigationName.ReviewEIDScreen}
            screenOptions={{
              headerShown: false,
            }}>
            <Stack.Screen
              name={NavigationName.BottomTabs}
              component={BottomTabNavigationERequest}
            />
            <Stack.Screen name={NavigationName.HomeScreen} component={Home} />
            <Stack.Screen
              name={NavigationName.ServiceDetail}
              component={ServiceDetail}
            />
            <Stack.Screen
              name={NavigationName.CreateTicket}
              component={CreateTicketScreen}
            />
            <Stack.Screen
              name={NavigationName.CreateTicketSuccess}
              component={CreateTicketSuccessScreen}
            />
            <Stack.Screen
              name={NavigationName.AddTicketRelativeS}
              component={AddTicketRelative}
            />
            <Stack.Screen
              name={NavigationName.BottomTabNavigationTicketDetail}
              component={TicketDetail}
            />
            <Stack.Group
              screenOptions={{headerShown: false, presentation: 'modal'}}>
              <Stack.Screen
                name={NavigationName.ViewFlowDetail}
                component={ViewFlowDetail}
              />
              {/* <Stack.Screen
                name={NavigationName.ViewFileScreen}
                component={ViewFileScreen}
              /> */}
            </Stack.Group>
            <Stack.Screen
              name={NavigationName.ReviewFlow}
              component={ReviewFlow}
            />
            <Stack.Screen
              name={NavigationName.ReviewEcontractFlow}
              component={ReviewEcontractFlow}
            />
            <Stack.Screen
              name={NavigationName.ViewFileScreenEcontract}
              component={ViewFileScreenEcontract}
            />
            <Stack.Screen
              name={NavigationName.SearchTicketScreen}
              component={SearchTicketScreen}
            />
            <Stack.Screen
              name={NavigationName.SearchHomeScreen}
              component={SearchHomeScreen}
            />
            <Stack.Screen
              name={NavigationName.ReviewEIDScreen}
              component={ReviewEIDScreen}
            />
            <Stack.Screen
              name={NavigationName.CreateContractScreen}
              component={CreateContractScreen}
            />
            <Stack.Screen
              name={NavigationName.ReviewEcollaboFlow}
              component={ReviewEcollaboFlow}
            />
            <Stack.Screen
              name={NavigationName.HomeListService}
              component={HomeListService}
            />
            <Stack.Screen
              name={NavigationName.NoAuthComponent}
              component={NoAuthComponent}
            />
          </Stack.Navigator>
          <Toast config={toastConfig} />
        </PaperProvider>
      </ApplicationProvider>
    </>
  );
};

export default AuthorizedNavigation;
