//import liraries
import React, {useEffect, useState} from 'react';
import {
  FlatList,
  Image,
  TouchableOpacity,
  useWindowDimensions,
  View,
} from 'react-native';
import styles from './styles';
import {useNavigation} from '@react-navigation/native';
import {isNull} from 'lodash';
import {Divider, Icon, List, TopNavigationAction} from '@ui-kitten/components';
import {useTranslation} from 'react-i18next';
import {useAppSelector} from '../../../../../hooks/useRedux';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import {Color, NavigationName, String} from '../../../../../constants';
import {IconDefault, IconEmpty} from '../../../../../assets/images';
import TextCM from '../../../../components/Text';
import HeaderCM from '../../../../components/Header/HeaderCM';
import LoadingMore from '../../../../components/LoadingMore';
import {TYPE_SCREEN} from '../../../../../constants/Size';
import Skeleton from '../../../../components/Skeleton';
import {IServicesHome} from '../../../../../model/interface/service.interface';

const HomeListService = () => {
  const {t} = useTranslation();
  const navigation = useNavigation<any>();
  const [listServices, setListServices] = useState<IServicesHome[]>([]);
  const [page, setPage] = useState(0);
  const [totalPages, setTotalPages] = useState(0);
  const [refreshing, setRefreshing] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [loadingMore, setLoadingMore] = useState(false);
  const {width} = useWindowDimensions();

  const {accessToken} = useAppSelector(state => state.common);
  const {dataAccount} = useAppSelector(state => state.account);
  const ListServices = useTicketServices();

  useEffect(() => {
    getListServices(0, false);
  }, []);

  const getListServices = async (page: number, isLoadMore: boolean) => {
    try {
      if (isLoadMore) {
        setLoadingMore(true);
      } else {
        setIsLoading(true);
      }
      const params = {
        sort: ['displayOrder,asc', 'createdDate,desc'],
        page: page,
        size: 50,
      };
      const roleIds = dataAccount?.roles?.map((r: any) => r.id).join(',');
      const body = {
        textSearch: '',
        roleIds: roleIds,
      };
      const data = await ListServices.getListServives(
        body,
        params,
        accessToken,
      );
      if (page === 0) {
        setListServices(data.content);
      } else {
        setListServices([...listServices, ...data.content]);
      }
      setTotalPages(data?.page?.totalPage);
    } catch (error) {
      console.log('error', error);
    } finally {
      setRefreshing(false);
      setIsLoading(false);
      setLoadingMore(false);
    }
  };

  const handleServiceDetail = (item: IServicesHome) => {
    navigation.navigate(NavigationName.ServiceDetail, item);
  };

  const renderContentRight = () => {
    return (
      <>
        <TopNavigationAction
          icon={<Icon fill={'#ffffff'} name="search" />}
          onPress={() => {
            navigation.navigate(NavigationName.SearchHomeScreen, {});
          }}
        />
      </>
    );
  };

  const renderListServiceItem = ({item}: {item: IServicesHome}) => {
    return (
      <TouchableOpacity
        onPress={() => handleServiceDetail(item)}
        style={[styles.ctnItemList, {flex: 1}]}>
        <View
          style={[
            styles.ctnImage,
            {
              backgroundColor: !isNull(item.color)
                ? item.color
                : Color.BgPrimary,
            },
          ]}>
          {!item.iconBase64 ? (
            <Icon name="grid-outline" fill={'#fff'} width={28} height={28} />
          ) : (
            <Image
              width={28}
              height={28}
              borderRadius={14}
              source={{
                uri: `${String.PREFIX_IMG_BASE64}${item.iconBase64}`,
              }}
            />
          )}
        </View>
        <View style={{flex: 1, rowGap: 8}}>
          <TextCM style={styles.textTitleItem}>{item.serviceName}</TextCM>
          <TextCM style={styles.textDes}>{item.description}</TextCM>
        </View>
      </TouchableOpacity>
    );
  };

  const renderContent = () => {
    if (isLoading && !loadingMore) {
      return (
        <List
          style={{
            backgroundColor: '#fff',
          }}
          data={Array(8).fill({})}
          renderItem={() => <Skeleton />}
          ItemSeparatorComponent={Divider}
        />
      );
    }

    return (
      <>
        {width > TYPE_SCREEN.tablet_h ? (
          <FlatList
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            data={listServices}
            renderItem={renderListServiceItem}
            keyExtractor={item => '_' + item.serviceId}
            key={'_'}
            numColumns={2}
            columnWrapperStyle={{columnGap: 20}}
            onEndReachedThreshold={0.5}
            onEndReached={fetchNextPage}
            refreshing={refreshing}
            onRefresh={onRefresh}
            ListEmptyComponent={
              <View style={styles.viewListServices}>
                <IconEmpty width={218} height={149} />
                <TextCM style={styles.textH1}>{t('HM.titleEmpty')}</TextCM>
                <TextCM>{t('HM.description')}</TextCM>
              </View>
            }
            ListFooterComponent={() => loadingMore && <LoadingMore />}
          />
        ) : (
          <FlatList
            showsHorizontalScrollIndicator={false}
            showsVerticalScrollIndicator={false}
            data={listServices}
            renderItem={renderListServiceItem}
            keyExtractor={item => '#' + item.serviceId}
            key={'#'}
            numColumns={1}
            onEndReachedThreshold={0.5}
            onEndReached={fetchNextPage}
            refreshing={refreshing}
            onRefresh={onRefresh}
            ListEmptyComponent={
              <View style={styles.viewListServices}>
                <IconEmpty width={218} height={149} />
                <TextCM style={styles.textH1}>{t('HM.titleEmpty')}</TextCM>
                <TextCM>{t('HM.description')}</TextCM>
              </View>
            }
            ListFooterComponent={() => loadingMore && <LoadingMore />}
          />
        )}
      </>
    );
  };

  const onRefresh = () => {
    setRefreshing(true);
    setPage(0);
    getListServices(0, false);
  };

  const fetchNextPage = () => {
    if (page < totalPages) {
      getListServices(page + 1, true);
      setPage(page + 1);
    }
  };

  return (
    <>
      <HeaderCM title="eRequest" renderContentRight={renderContentRight} />
      <View style={styles.ctnMain}>
        <TextCM style={styles.textTitle}>{t('HM.title')}</TextCM>
        {renderContent()}
      </View>
    </>
  );
};

//make this component available to the app
export default HomeListService;
